@import '../../style.scss';

.navBar {
	@include themify($themes) {
		display: flex;
		justify-content: space-between;
		padding: 10px 20px;
		height: 70px;
		border-bottom: 1px solid themed('border');
		position: sticky;
		top: 0;
		background-color: themed('bg');
		color: themed('textColor');
		z-index: 10;

		.left {
			display: flex;
			align-items: center;
			gap: 30px;
			@include tablet {
				gap: 20px;
			}

			span {
				font-weight: bold;
				font-size: 20px;
				color: themed('logo');
			}
			.search {
				display: flex;
				align-items: center;
				gap: 10px;
				border: 1px solid themed('border');
				padding: 5px;
				input {
					border: none;
					width: 400px;
					background-color: transparent;
					color: themed('textColor');

					@include mobile {
						display: none;
					}
					@include tablet {
						width: 100%;
					}
				}
			}
		}
		.right {
			display: flex;
			align-items: center;
			gap: 20px;

			@include mobile {
				display: none;
			}
			@include tablet {
				gap: 15px;
			}

			.user {
				display: flex;
				align-items: center;
				gap: 10px;
				font-weight: 600;

				img {
					width: 30px;
					height: 30px;
					border-radius: 50%;
					object-fit: cover;
				}
				span {
					font-weight: 700;
				}
			}
		}
	}
}
