@import '../../style.scss';

.profile {
	flex: 6;
	@include themify($themes) {
		background-color: themed('bgSoft');

		.images {
			width: 100%;
			height: 300px;
			position: relative;

			.cover {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			.profilePict {
				width: 180px;
				height: 180px;
				border-radius: 50%;
				object-fit: cover;
				position: absolute;
				left: 0;
				right: 0;
				margin: auto;
				top: 170px;
			}
		}
		.profileCont {
			padding: 20px 40px;

			@include mobile {
				padding: 10px;
			}
			@include tablet {
				padding: 20px;
			}
			.usInfo {
				height: 180px;
				-webkit-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
				-moz-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
				box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
				border-radius: 20px;
				background-color: themed('bg');
				color: themed('textColor');
				padding: 14% 4%;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-bottom: 20px;

				@include mobile {
					flex-direction: column;
					height: 30vh;
					padding: 20px;
					margin-top: 50px;
				}
				.left {
					flex: 1;
					display: flex;
					gap: 10px;

					@include tablet {
						flex-wrap: wrap;
					}

					a {
						color: themed('textColorSoft');
						cursor: pointer;
					}
				}
				.center {
					flex: 1;
					display: flex;
					flex-direction: column;
					align-items: center;
					span {
						font-size: 30px;
						font-weight: 500;
					}
					.info {
						width: 100%;
						display: flex;
						align-items: center;
						justify-content: space-around;
						margin-top: 10px;
						gap: 10px;

						.item {
							display: flex;
							align-items: center;
							color: themed('textColorSoft');
						}
						span {
							font-size: 12px;
						}
					}
					button {
						background-color: #5271ff;
						color: white;
						padding: 10px 20px;
						border-radius: 5px;
						margin-top: 10px;
					}
				}
				.right {
					flex: 1;
					display: flex;
					align-items: center;
					justify-content: flex-end;
					gap: 10px;
				}
			}
		}
	}
}
